import { FormControl } from '@angular/forms';

export class CustomValidators {

  static noWhiteSpaces(c: FormControl) {

    return c.value.trim() != "" ? null : {
      validateWhiteSpaces: {
        valid: false
      }
    };
  }

  static onlyLettersPattern = /^[a-zA-Z\- àâäèéêëîïôœùûüÿçÀÂÄÈÉÊËÎÏÔŒÙÛÜŸÇ]*$/;
  static emailPattern = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
}



