import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})


export class AppComponent {

  title = 'Simple form :';
  formTitle = 'Inscription';
  step = 1;
  postData = {};
  dataStep1 = {};
  dataStep2 = {};
  fullData = {};
  stepsValid = [true, false, false];

  goSignin = () => {
    this.formTitle = "Test";
  };
  goLogin = () => {
    this.formTitle = "Connexion";
  };

  goStep = (index) => {
    if(this.stepsValid[index]){
      this.step = index + 1;
    }
  }

  goStep2 = (postData) => {
    if(this.stepsValid[1]){
      this.dataStep1 = postData;
      this.postData = Object.assign(this.dataStep1, this.dataStep2)
      this.step = 2;
    }
  }

  goStep3 = (postData) => {
    if(this.stepsValid[1] && this.stepsValid[2]){
      this.dataStep2 = postData;
      this.postData = Object.assign(this.dataStep1, this.dataStep2)
      this.step = 3;
    }
  }

  handleStepValid = (val, stepIndex) => {
    this.stepsValid[stepIndex + 1] = val;
  }

}



