import { Component, OnInit, Input, Output, EventEmitter, trigger, state, transition, style, animate, PipeTransform, Pipe, SimpleChanges } from '@angular/core';
import { CustomPolyfills } from '../custom-polyfills'


@Component({
  selector: 'app-form3',
  templateUrl: './form3.component.html',
  styleUrls: ['./form3.component.css'],
  animations: [
    trigger('slideState', [
      state('inactive', style({
        backgroundColor: '#eee',
        transform: 'translateX(200%)'
      })),
      state('active',   style({
        transform: 'translateX(0%)',
      })),
      transition('inactive => active', animate('800ms ease-in')),
      transition('active => inactive', animate('800ms ease-out'))
    ])
  ]
})
export class Form3Component implements OnInit {
  @Input() postData;
  arrayKeys = null;

  @Input() currentStep;

  constructor() { }

  ngOnInit() {
  }

  ngDoCheck(){
    this.arrayKeys = Object.keys(this.postData)
  }

  ngOnChanges(changes: SimpleChanges){
    console.log('changes')
    this.arrayKeys = Object.keys(this.postData)
  }

  submitForm(){
    let customEvent = CustomPolyfills.createNewEvent('submit-ng')
    console.log('-> Event :', customEvent);
    window.dispatchEvent( customEvent );
  }

}



