import { Component, OnInit, Input, Output, EventEmitter, trigger, state, transition, style, animate, HostBinding } from '@angular/core';
import { FormBuilder, Validators, FormControl } from '@angular/forms';
import { CustomValidators } from '../custom-validators'

@Component({
  selector: 'app-form2',
  templateUrl: './form2.component.html',
  styleUrls: ['./form2.component.css'],
  animations: [
    trigger('slideState', [
      state('inactivePrevious', style({
        transform: 'translateX(-200%)',
        display: 'none'
      })),
      state('inactiveNext', style({
        transform: 'translateX(200%)',
        display: 'none'
      })),
      state('active',   style({
        transform: 'translateX(0%)',
        display: 'block'
      })),
      transition('inactivePrevious => active', animate('800ms ease-in')),
      transition('inactiveNext => active', animate('800ms ease-in')),
      transition('active => inactivePrevious', animate('100ms ease-out')),
      transition('active => inactiveNext', animate('400ms'))
    ])
  ]
})
export class Form2Component implements OnInit {
  // @HostBinding('style.position')  position = 'absolute';
  // @HostBinding('style.left')  left = '0px';
  // @HostBinding('style.top')  top = '0px';
  // @HostBinding('style.width')  width = '100%';

  @Input()
  formTitle: String;

  @Input() goStep3;

  @Input() currentStep;

  @Output() stepValid = new EventEmitter();

  constructor(public fb: FormBuilder) {}
  public step2Form = this.fb.group({
    
    'gender-tpz': [null, Validators.required],
    power: ['', Validators.required],
    comments: ['', [Validators.required, CustomValidators.noWhiteSpaces]],
    newsletter: [false]
  });
  title = 'Simple form :';
  formData = {newsletter: false, power: ''};
  powers = [{label: 'Emploi 1', value: '1'}, {label: 'Emploi 2', value: '2'},
            {label: 'Emploi 3', value: '3'}, {label: 'Emploi 4', value: '4'}];
  slideState = 'inactiveNext';
  
  testFct = function(){
    // //console.log(this.mockForm);
    
    // console.log('BLLLLAAH', this.step2Form.get('lstName'))
    // console.log('=> Signin form value', this.step2Form.value)
  }

  ngOnInit(){

  }

  validStep2(){
    this.goStep3(this.step2Form.value);
  }

  ngDoCheck(){
    if(this.currentStep == 1){
      this.slideState = "inactiveNext";
    }
    else if(this.currentStep == 3){
      this.slideState = "inactivePrevious";
    }
    else if(this.currentStep == 2){
      this.slideState = 'active';
    }
    this.stepValid.emit(this.step2Form.valid)
  }

}



